        } else if (RAO > 14.000) {
            let RAS = boxA[1];
            let limit = Randomnum(100);
            let ot = Randomnum2(5);
            if (limit < 85) {
                RA = RAS[Randomnum2(6)];
            } else { 
                RA = RAS[(RAS.length - (1 + ot))];
            }
            
        } else if (RAO > 13.000) {
            RA = boxA[2][0];

        } else if (RAO > 6.500) {
            RA = boxA[3][0];
            
        } else if (RAO > 3.000) {
            RA = boxA[4][0];
            
        } else if (RAO > 1.000) {
            let RAS = boxA[5];
            let limit = Randomnum(100);
            if (limit > 85.000) {
                RA = RAS[1];
            } else { 
                RA = RAS[0];
            }        
        } else if (RAO > 0) {
            let RAS = boxA[6];
            let limit = Randomnum(100);
            if (limit > 85.000) {
                RA = RAS[1];
            } else { 
                RA = RAS[0];
            }        
        }

        //B类
        let RBO = Randomnum (100); 
        if (RBO > 34.340) {
            let RBS = boxB[0];
            let limit = Randomnum(100);
            let ot = Randomnum2(8);
            if (limit < 85.000) {
                RB = RBS[Randomnum2(8)];
            } else { 
                RB = RBS[(RBS.length - (1 + ot))];
            }
        } else if (RBO > 11.340) {
            let RBS = boxB[1];
            RB = RBS[Randomnum2(RBS.length)];
           
        } else if (RBO > 2.810) {
            let RBS = boxB[2];
            let limit = Randomnum(100);
            // console.log('强化：' + limit);
            //0.005%
            if (limit > 99.995) {
                let ot = Randomnum2(2);
                RB = RBS[(RBS.length - (1 + ot))];
            } 
            // 0.01%
            else if (limit > 99.990) {
                let ot = Randomnum2(3);
                RB = RBS[(RBS.length - (3 + ot))];
            }
            //0.05%
            else if (limit > 99.950) {
                let ot1 = Randomnum2(3);
                RB = RBS[(RBS.length - (6 + ot1))];
            } 
            //0.1%
            else if (limit > 99.900) {
                let ot2 = Randomnum2(3);
                RB = RBS[(RBS.length - (9 + ot2))];
            } 
            //0.5%
            else if (limit > 99.500) {
                let ot3 = Randomnum2(3);
                RB = RBS[(RBS.length - (12 + ot3))];
            } 
            //4%
            else if (limit > 96.000) {
                let ot3 = Randomnum2(3);
                RB = RBS[(RBS.length - (15 + ot3))];
            } 
            //8%
            else if (limit > 92.000) {
                let ot3 = Randomnum2(3);
                RB = RBS[(RBS.length - (18 + ot3))];
            } 
            //16%
            else if (limit > 84.000) {
                let ot3 = Randomnum2(3);
                RB = RBS[(RBS.length - (21 + ot3))];
            } 
            //35%
            else if (limit > 65.000) {
                let ot3 = Randomnum2(3);
                RB = RBS[(RBS.length - (24 + ot3))];
            } 
            else {
                RB = RBS[Randomnum2(4)];
            }

        } else if (RBO > 0) {
            let RBS = boxB[3];
            let limit = Randomnum(100);
            // console.log('增幅：' + limit);
            //0.005%
            if (limit > 99.995) {
                RB = RBS[(RBS.length - 1)];
            } 
            // 0.01%
            else if (limit > 99.990) {
                let ot = Randomnum2(2);
                RB = RBS[(RBS.length - (2 + ot))];
            }
            //0.05%
            else if (limit > 99.950) {
                let ot1 = Randomnum2(2);
                RB = RBS[(RBS.length - (4 + ot1))];
            } 
            //0.1%
            else if (limit > 99.900) {
                let ot2 = Randomnum2(4);
                RB = RBS[(RBS.length - (6 + ot2))];
            } 
            //0.5%
            else if (limit > 99.500) {
                let ot3 = Randomnum2(7);
                RB = RBS[(RBS.length - (10 + ot3))];
            } 
            //4%
            else if (limit > 96.000) {
                let ot3 = Randomnum2(4);
                RB = RBS[(RBS.length - (17 + ot3))];
            } 
            //8%
            else if (limit > 92.000) {
                let ot3 = Randomnum2(3);
                RB = RBS[(RBS.length - (21 + ot3))];
            } 
            //16%
            else if (limit > 84.000) {
                let ot3 = Randomnum2(3);
                RB = RBS[(RBS.length - (24 + ot3))];
            } 
            else {
                RB = RBS[Randomnum2(3)];
            }
        }

        //C类
        let RCO = Randomnum (100); 
        if (RCO > 12.500) {
            let RCS = boxC[0];
            let limit = Randomnum(100);
            if (limit > 99.998) {
                RC = RCS[(RCS.length - 1)];
            } else if (limit > 99.980) {
                let ot = Randomnum2(2);
                RC = RCS[(RCS.length - (2 + ot))];
            } else if (limit > 99.900) {
                RC = RCS[(RCS.length - 4)];
            } else if (limit > 99.750) {
                RC = RCS[(RCS.length - 5)];
            } else if (limit > 99.600) {
                RC = RCS[(RCS.length - 6)];
            } else {
                RC = RCS[Randomnum2(RCS.length - 7)];
            }
            
        } else if (RCO > 4.500) {
            let RCS = boxC[1];
            RC = RCS[Randomnum2(RCS.length)];
           
        } else if (RCO > 4.000) {
            let RCS = boxC[2];
            RC = RCS[Randomnum2(RCS.length)];
           
        } else if (RCO > 2.500) {
            let RCS = boxC[3];
            RC = RCS[Randomnum2(RCS.length)];
           
        } else if (RCO > 1.000) {
            let RCS = boxC[4];
            let limit = Randomnum(100);
            if (limit > 99.500) {
                RC = RCS[RCS.length - 1];
            } else {
                RC = RCS[Randomnum2(4)];
            }
           
        } else if (RCO > 0) {
            let RCS = boxC[5];
            let limit = Randomnum(100);
            if (limit > 99.950) {
                RC = RCS[RCS.length - 1];
            } else {
                RC = RCS[Randomnum2(2)];
            }
            
          
        }
        // console.log(this.count);

        if (this.count % 9 == 0) {

            // RA.amount = RA.amount * 2;
            // RB.amount = RB.amount * 2;
            // RC.amount = RC.amount * 2;
            // console.log(RA.amount);
            
            this.double.unshift(RA);
            this.double.unshift(RB);
            this.double.unshift(RC);
        }
        if (this.count % 9 != 0) {
            this.result.unshift(RA);
            this.result.unshift(RB);
            this.result.unshift(RC);
        }

    },
    // //按对象属性来排序
    // compare :function (property)  {
    //    return function(a,b){
    //     var value1 = a[property];
    //     var value2 = b[property];
    //     return value1 - value2;
    //   }
    // },
    addtotal: function() {

      //提取道具标签
      this.result.forEach((item)=>{

        if (!this.totalward[item.name]) {//利用对象key索引，判断是否已有该标签分类字段
          this.totalward[item.name] = {//没有则创建key索引
            'name': item.name,
            'amount': item.amount,
            'color': item.color
            }
        } else {//有则增加该重复次数统计
             this.totalward[item.name].amount += item.amount;
        }
      });
      //双倍
      this.double.forEach((item)=>{
        if (!this.totalward[item.name]) {
          this.totalward[item.name] = {
            'name': item.name,
            'amount': item.amount * 2,
            'color': item.color
            }
        } else {
            this.totalward[item.name].amount += item.amount * 2;
        }
      });

      // console.log(this.totalward)
      
    },
    msgplay (){
         const msg = document.getElementById('msg');
         msg.play();
   },
    one: function() {
      this.result = [];
      this.double = [];
      this.chouqu();
      this.addtotal();
      this.msgplay();
    },
    ten: function() {
      this.result = [];
      this.double = [];
      for (let i=0; i<10; i++) {
        this.chouqu();
      }
      this.addtotal();
      this.msgplay();
    },
    hundred: function() {
      this.result = [];
      this.double = [];
      for (let i=0; i<100; i++) {
        this.chouqu();
      }
      this.addtotal();
      this.msgplay();
      
    }
  },
  name: 'App',

  components: {

  }
}
</script>

